<template>
    <div>
      <div id="app">
        <div class="container">
          <div class="panel panel-sm">
            <div class="panel-heading"> 
              <h4>CSV Import</h4>
            </div>
            <div class="panel-body">
              <div class="form-group">
                <label for="csv_file" class="control-label col-sm-3 text-right">CSV file to import</label>
                <div class="col-sm-9">
                  <input type="file" id="csv_file" name="csv_file" class="form-control" @change="loadCSV($event)">
                </div>
              </div>
              <div class="col-sm-offset-3 col-sm-9">
                <div class="checkbox-inline">
                  <label for="header_rows"><input type="checkbox" id="header_rows"> File contains header row?</label>
                </div>
              </div>
            </div>
          </div>
        </div>

        <vs-button color="primary" @click="print">Print</vs-button>
        <div class="wrapper-print" id="printMe">
          <!-- Invoice -->
          <div class="invoice" style="margin-bottom: 50px" v-for="(item,index) in parse_csv" :key="index">
            <!-- Head -->
            <div class="flex">
              <div class="flex-initial text-gray-700 ml-2 mt-2 mb-2">
                <h6 class="font-bold">PT Selahonje Jaya Makmur</h6>
                <h6 class="font-bold">Ruko Symphoni Blok HX 2 No.1</h6>
                <h6 class="font-bold">Kota Harapan Indah, Bekasi</h6>
              </div>
              <div class="flex-1 text-gray-700 text-center px-4 py-2">
                <h6 class="font-bold">SLIP GAJI</h6>
              </div>
              <div class="flex-1 text-gray-700 text-center px-4 m-2">
                <img src="logo_perusahaan.png" alt="Logo" style="width:100px">
              </div>
            </div>
            <!-- Name and Jabatan -->
            <div class="grid grid-cols-6 gap-4 my-1">
              <div class="col-start-1 col-end-3">
                  <div class="flex">
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      NAMA
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.name}}
                    </div>
                  </div>
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mt-2 mb-2 ml-2" style="margin-right: 29px">
                      NIK
                    </div>
                    <div class="flex-initial text-gray-700 m-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 m-2">
                    {{item.nik}}
                    </div>
                  </div>
              </div>
              <div class="col-end-8 col-span-3">
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      JABATAN
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.jabatan}}
                    </div>
                  </div>
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mt-2 mb-2 ml-2" style="margin-right: 21px">
                      BULAN
                    </div>
                    <div class="flex-initial text-gray-700 m-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 m-2">
                    {{item.bulan}}
                    </div>
                  </div>
              </div>
            </div>
            <!-- Title -->
            <div class="grid grid-cols-6 gap-4">
              <div class="col-start-1 col-end-3">
                <div class="flex">
                  <div class="flex-initial text-gray-700 ml-2 mr-2 mt-2">
                  PENDAPATAN
                  </div>
                </div>
              </div>
              <div class="col-end-8 col-span-3">
                <div class="flex">
                  <div class="flex-initial text-gray-700 ml-2 mr-2 mt-2">
                  POTONGAN
                  </div>
                </div>
              </div>
            </div>
            <!-- Content -->
            <div class="grid grid-cols-6 gap-4 mt-3">
              <div class="col-start-1 col-end-4">
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2" style="margin-right: 42px">
                      GAJI POKOK
                    </div>
                    <div class="flex-initial text-gray-700 ml-1 mr-1">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.gapok === ' - ' ? item.gapok : 'Rp.' + item.gapok }}
                    </div>
                  </div>
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2 mr-6">
                      TUNJ. MAKAN
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.tnj_makan === ' - ' ? item.tnj_makan : 'Rp.' + item.tnj_makan }}
                    </div>
                  </div>
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2" style="margin-right: 7px">
                      TUNJ. KEAHLIAN
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.tnj_keahlian === ' - ' ? item.tnj_keahlian : 'Rp.' + item.tnj_keahlian }}
                    </div>
                  </div>
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2" style="margin-right: 66px">
                      LEMBUR
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.lembur === ' - ' ? item.lembur : 'Rp.' + item.lembur }}
                    </div>
                  </div>
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2" style="margin-right: 81px">
                      RAPEL
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.rapel === ' - ' ? item.rapel : 'Rp.' + item.rapel }}
                    <hr class="my-2" style="border: solid 1px black">
                      <h6 class="font-bold">Rp. {{item.total_pendpatan}}</h6>
                    </div>
                  </div>
              </div>
              <div class="col-end-8 col-span-3">
                <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2" style="margin-right: 39px">
                      JAMSOSTEK
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.jamsostek === ' - ' ? item.jamsostek : 'Rp.' + item.jamsostek }}
                    </div>
                </div>
                <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2" style="margin-right: 4px">
                      BPJS KESEHATAN
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.bpjs === ' - ' ? item.bpjs : 'Rp.' + item.bpjs }}
                    </div>
                </div>
                <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2" style="margin-right: 73px">
                      PPH 21
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.pph_21 === ' - ' ? item.pph_21 : 'Rp.' + item.pph_21 }}
                    </div>
                </div>
                <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 mb-2 ml-2" style="margin-right: 110px">
                      JP
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      :
                    </div>
                    <div class="flex-initial text-gray-700 ml-2 mr-2">
                      {{item.jp === ' - ' ? item.jp : 'Rp.' + item.jp }}
                      <hr class="my-2" style="border: solid 1px black">
                      <h6 class="font-bold">Rp. {{item.total_pengeluaran}}</h6>
                    </div>
                </div>
              </div>
              <div class="col-start-2 col-span-4 mx-auto mb-3">
                  <div class="flex bg-gray-200">
                    <div class="flex-initial text-gray-700 text-center m-2">
                      <h6 class="font-bold">TAKE HOME PAY</h6>
                    </div>
                    <div class="flex-initial text-gray-700 text-center m-2">
                      <h6 class="font-bold">:</h6>
                    </div>
                    <div class="flex-initial text-gray-700 text-center m-2">
                    <h6 class="font-bold">Rp. {{item.take_home_pay}}</h6>
                    </div>
                  </div>
                  <hr class="-mt-1" style="border: solid 1px black">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
    data() {
      return{
        parse_header: [],
        parse_csv: [],
      }
    },

    methods: {
        print () {
          // Pass the element id here
          this.$htmlToPaper('printMe');
        },
        
        csvJSON(csv){
            let lines = csv.split("\n")
            let result = [];
            
            let parse_row_one = lines[0].split(";")
            let bulan = parse_row_one[1]
            // console.log(bulan);

            for(let i = 0 ; i < lines.length; i++)
            {
              if(lines[i].length > 0) {
                if(lines[i].charAt(0) > 0) {

                  let parse_row = lines[i].split(";")

                  let name = parse_row[1];
                  // console.log(name);
                  let nik = parse_row[2];
                  // console.log(nik);
                  let jabatan = parse_row[3];
                  // console.log(jabatan);
                  let gapok = parse_row[4];
                  // console.log(gapok);
                  let tnj_makan = parse_row[5];
                  // console.log(tnj_makan);
                  let tnj_keahlian = parse_row[6];
                  // console.log(tnj_keahlian);
                  let lembur = parse_row[7];
                  // console.log(lembur);
                  let rapel = parse_row[8];
                  // console.log(rapel);
                  let total_pendpatan = parse_row[9];
                  // console.log(total_pendpatan);
                  let jamsostek = parse_row[10];
                  // console.log(jamsostek);
                  let bpjs = parse_row[11];
                  // console.log(bpjs);
                  let pph_21 = parse_row[12];
                  // console.log(pph_21);
                  let jp = parse_row[13];
                  // console.log(jp);
                  let total_pengeluaran = parse_row[14];
                  // console.log(total_pengeluaran);
                  let take_home_pay = parse_row[15];
                  // console.log(take_home_pay);

                  let obj = {
                    bulan : bulan,
                    name : name,
                    nik : nik,
                    jabatan : jabatan,
                    gapok : gapok,
                    tnj_makan : tnj_makan,
                    tnj_keahlian : tnj_keahlian,
                    lembur : lembur,
                    rapel : rapel,
                    total_pendpatan : total_pendpatan,
                    jamsostek : jamsostek,
                    bpjs : bpjs,
                    pph_21 : pph_21,
                    jp : jp,
                    total_pengeluaran : total_pengeluaran,
                    take_home_pay : take_home_pay
                  }
                  result.push(obj)
                }
              }
            }
            
            return result;
            
        },

        loadCSV(e) {
          var vm = this
          if (window.FileReader) {
              var reader = new FileReader();
              reader.readAsText(e.target.files[0]);
              // Handle errors load
              reader.onload = function(event) {
              var csv = event.target.result;
              vm.parse_csv = vm.csvJSON(csv)
              // console.log(vm.parse_csv)
              };
              reader.onerror = function(evt) {
              if(evt.target.error.name == "NotReadableError") {
                  alert("Canno't read file !");
              }
              };
          } else {
              alert('FileReader are not supported in this browser.');
          }
        }
    }
}

</script>

<style>
.panel {
  border: 2px solid #dfdfdf;
  box-shadow: rgba(0, 0, 0, 0.15) 0 1px 0 0;
  margin: 10px;
} 
.panel.panel-sm {
  max-width: 100%;
  margin: 10px auto;
}
.panel-heading {
  border-bottom: 2px solid #dfdfdf;
}
.panel-heading h1, .panel-heading h2, .panel-heading h3, .panel-heading h4, .panel-heading h5, .panel-heading h6 {
  margin: 0;
  padding: 0;
}
.panel-body .checkbox-inline {
  padding: 15px 20px;
}

.wrapper-print {
  overflow-y: scroll;
  height: 560px;
}

.invoice {
  width: 800px;
  border: 2px solid #000000;
  margin: auto;
  padding: 5px;
}
.invoice-table p {
  padding: 0;
}  
.invoice .invoice-sec-1-date .invoice-logo {
  width: 100%;
}
.invoice .invoice-sec-1-date .invoice-logo img {
  display: inline;
  width: 80px;
}
</style>
